import React from 'react';
import { Row, Col } from "react-bootstrap";
import './Sites.css';
import '../../UI/Spinner/spinner.css';
import img from '../../../images/center/artur.jpg';
import {datasites} from './datasites';



const sitesData = (props) => {

    if (props.sitesDB.length > 0 && props.sitesDB[props.selectedSiteIndex]) {
        return (
            <Row>
                <Col>
                    <h5>{props.sitesDB[props.selectedSiteIndex].name} </h5>
                    <img style={{ width: '15rem', paddingTop: "6px", paddingRight: "20px", float: "left" }}
                        src={datasites[props.selectedSiteIndex].src} />
                    
                    <p>{props.sitesDB[props.selectedSiteIndex].description}</p>
                    <p>איך מגיעים:{props.sitesDB[props.selectedSiteIndex].location}</p>
                </Col>
            </Row>
        )
    }
    else {
        return (
            <div className="spinnercontainer">
            
                <div className="spinner">
                    <div className="spinner-item"></div>
                    <div className="spinner-item"></div>
                    <div className="spinner-item"></div>
                </div>

         </div>
            // <div className="Loader"></div>
        );
    }
};



export default sitesData;




