import React from 'react';
import './Landing.css';
import {
    BrowserRouter as Router,
   Link
} from "react-router-dom";

const selectarea = (props) => (
    <div id="selectarea"
        style={{
            display: props.show ? 'block' : 'none'
        }}>
        <h2>בחר איזור בארץ</h2>
        <ul>
        <li><Link to="/area=south" areaname="south"  >
                <button className="areabutton"> דרום</button>
            </Link></li>
           
            <li><Link to="/area=center" areaname="center"  >
                <button className="areabutton"> מרכז </button>
            </Link></li>
            
            <li> <Link to="/area=north" areaname="north">
                <button className="areabutton"> צפון </button>
            </Link></li>
            
        </ul>
    </div>
 )

export default selectarea;